export default (state = {}, action) => {
	switch (action.type) {
	case "SET_CONTEXT_MENU_DEF": {
		const menuDef = action.data.menuDef.map((m) => Object.assign({}, m));
		return Object.assign({}, state, { menuDef: menuDef });
	}

	default:
		return state;
	}
};
